$main-color: #ec407a;
$highlight-color: #ef5350;
$academic-font: "Garamond", serif;

*{
    margin:0px;
    padding: 0px;
    font-family: sans;
}
body{
    transition: ease .5s;
    background: #fbfbfb;
    
}
a{
    text-decoration: none;
    color: #3f51b5;
}
a:hover{
    text-decoration: underline;
}
footer{
    background: #212121;
    padding: 20px;
    color: white;
    font-size: 0.8rem;
}
.wrapper{
    padding: 20px 10%;
    box-sizing: border-box;
}
.fixed-top{
    position: fixed;
    top:20px;
    right: 10%;
    width: 20%;
}
.row{
    display: table;
    width: 100%;
    border-collapse: separate;
    border-spacing: 10px 5px;
    .col{
        width: 100%;
        height: 100%;
    }
    .col-1-4, .col-3-4, .col-1-5, .col-4-5{
        display: table-cell;
        vertical-align: middle;
    }
    .col-1-4{
        width: 25%;
    }
    .col-3-4{
        width: 75%;
    }
    .col-1-5{
        width: 20%;
    }
    .col-4-5{
        width: 80%;
    }
}
div.highlight{
    background-color: $highlight-color;
    border: 1px solid black;
}
span.highlight{
    color: $highlight-color;
}

.block{
    min-height: 100px;
    .sec{
        height: 100%;
        // background-color: #ccc;
        .title{
            font-size: 1.6rem;
            margin: 20px 0px;
            font-weight: bold;
            font-family: sans;
        }
        .content{
            text-align: justify;
        }
    }
}

.img-board{
    width: 50px;
    height: 50px;
    margin:5px auto;
    position: relative;
    .img, .cover{
        position: absolute;
        width: 100%;
        height: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 100px;
        transition: ease 0.3s;
    }
    .img{
        top:0px;
        left: 0px;
        z-index: 2;
        // border: 1px solid #DDD;
        box-sizing: border-box;

    }
    .cover{
        opacity: 0;
        top:10px;
        left: 0px;
        z-index: 1;
        filter:blur(20px);
    }
}
.img-board:hover{
    .img{
        transform: scale(1.05);
        border:0px;
    }
    .cover{
        opacity: 1;
    }
}

.profile{
    font-size: 1.1rem;
    *{font-family: serif !important;}
    transition: ease .5s;
    background-color: white;
    padding: 10px;
    box-sizing: border-box;
    border-radius: 1em;
    box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
    .zh_name{transition: ease .5s;}
    .img-board{
        width: 150px;
        height: 150px;
        margin:20px auto;
        *{border-radius: 10px;opacity: 1;border:none;}
    }
    .name{
        font-weight: bold;
        font-size: 1.6rem;
    }
    .dot{
        position: relative;
        height:10px;
    }
    .dot::before{
        content: "";
        height: 8px;
        width: 8px;
        margin: 1px;
        position: absolute;
        background: black;
        border-radius: 10px;
    }
    .line{
        position: relative;
        height:1px;
        margin:10px 0px;
    }
    .line::before{
        content: "";
        height: 1px;
        width: 100%;
        margin: 1px;
        position: absolute;
        top: 0px;
        left: 0px;
        background: #ddd;
    }
    .email, .github{
        font-size: 14px;
        line-height: 24px;
        display: contents;
        .logo{
            height: 20px;
            width: 20px;
            margin:2px;
        }
    }
}

.logo{
    height: 24px;
    width: 24px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: block;
    margin: auto;
    // background-color: #EEE;
        border-radius: 5px;
}

ul{
    list-style: none;
}
li{
    margin:5px;
    padding: 5px 10px;
    box-sizing: border-box;
    transition: ease .5s;
    background-color: white;
    border-radius: 10px;
}
.news{
    li{
        .date{float:left; font-weight: bold;}
        .event{
            text-indent: 10px;
        }
    }
}
.honor{
    li{
        .date{float:right;}
        .date, .event{font-weight: bold;}
        // .event{
        //     text-indent: 10px;
        // }
        .ins{font-family: serif;}
    }
}

.education{
    li{
        float: left;
        position: relative;
        width: calc(33% - 10px);
        padding: 5px 0px;
        padding-top: 10px;
        transition: ease .5s;
        // box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
        *{font-size: 0.9rem;}
        .ins{
            font-weight: bold;
            font-family: serif;
            font-size: 1.1rem;
        }
        .duration{position: absolute; top:0px; right:0px;color: #bbb;font-size:0.6rem; width: 100%;text-align: center;}
        .img-board{
            width: 80px;
            height: 80px;
            *{
                background-size: contain;
                border-radius: 0px;
                border:none;
            }
        }
        .remark-b{margin-top:5px;background-color: #fbfbfb;padding:5px;border-radius:5px;color:#AAA;}
    }
}

.paper{
    .img-board{
        height: 140px;
        width: 100%;
        *{
            background-size: contain;
            border-radius: 0px;
            border:none;
        }
    }
    *{font-size: 0.9rem;}
    ul .title{
        font-size: 1.1rem !important;
        margin: 0px !important;
    }
    .logo{
        float: left;
        margin: 10px calc(25% - 12px);
        // background-color: #EEE;
        border-radius: 5px;
    }
    .remark{
        color: #aaa;
    }
}

.links{
    li{margin:0px;background-color: transparent !important;font-size: 16px;padding:2px 10px;}
}

.btn-mode{
    position: fixed;
    height: 50px;
    width: 50px;
    background: #666;
    bottom:100px;
    right: 20px;
    border-radius: 50px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 50%;
    box-shadow: 0px 5px 20px -3px rgba(0,0,0,0.1);
    cursor: pointer;

}
.btn-mode.todark{
    background-color: white;
}
strong{
    color: $main-color;
}

a{
    color: $main-color;
    position: relative;
}

.tolight{
    display: none;
}
.todark{
    display: block;
}